import React from "react";

import styles from "./style.module.css";
import cart from "../../static/icons/cart.svg";
import { NavLink } from "react-router-dom";

export class JwModal extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      isOpened: false,
    };

    this.wrapperRef = React.createRef();
    this.handleClickOutside = this.handleClickOutside.bind(this);
  }

  componentDidMount() {
    document.addEventListener("mousedown", this.handleClickOutside);
  }

  componentWillUnmount() {
    document.removeEventListener("mousedown", this.handleClickOutside);
  }

  toggleState = () => {
    this.setState({ isOpened: !this.state.isOpened });
  };

  handleClickOutside(event) {
    if (this.wrapperRef && !this.wrapperRef.current?.contains(event.target)) {
      this.setState({ isOpened: false });
    }
  }

  totalQuantity = () =>
    this.props.cartProducts.reduce((sum, product) => sum + product.amount, 0);

  totalSymbol = () =>
    this.props.cartProducts[0]?.price?.currency?.symbol || "$";

  productsPrice = () =>
    this.props.cartProducts
      .reduce((sum, product) => sum + product.amount * product.price.amount, 0)
      .toFixed(2);

  render() {
    const cartProducts = this.props.cartProducts;
    return (
      <div>
        <div className={styles.header_cart} onClick={this.toggleState}>
          <button className={styles.header_btn}>
            <img src={cart} alt="cart" />
          </button>
          {cartProducts.length > 0 ? (
            <p className={styles.header_cart_badge}>{cartProducts.length}</p>
          ) : (
            ""
          )}
        </div>
        {this.state.isOpened && (
          <>
            <div ref={this.wrapperRef}>
              <div className={styles.dialog_modal}>
                <div className={styles.dialog_modal_body}>
                  <h2 className={styles.dialog_bag}>
                    My Bag,{" "}
                    <span>
                      {this.totalQuantity()}{" "}
                      {this.totalQuantity() === 1 ? "item" : "items"}
                    </span>
                  </h2>
                  <div>
                    {cartProducts?.map((product) => {
                      return <>{/* <img src={product.gallery[0]} /> */}</>;
                    })}
                  </div>
                  <div className={styles.dialog_price_wrapper}>
                    <p className={styles.dialog_total}>Total</p>
                    <p className={styles.dialog_price}>
                      {this.totalSymbol()}
                      {this.productsPrice()}
                    </p>
                  </div>
                  <div className={styles.dialog_btn_wrapper}>
                    <NavLink to="/cart">
                      <button className={styles.dialog_btn_cart}>
                        View bag
                      </button>
                    </NavLink>
                    <button className={styles.dialog_btn_order}>
                      Check out
                    </button>
                  </div>
                </div>
              </div>
            </div>
            <div className={styles.dialog_modal_background}></div>
          </>
        )}
      </div>
    );
  }
}
