import React from "react";
import { NavLink } from "react-router-dom";

import CartStore from "../../core/store/cart";
import CategoriesStore from "../../core/store/categories";
import CurrencyStore from "../../core/store/currency";
import { SelectCurrency } from "../selectCurrency";
import { CategoriesService } from "../../core/services/categories.service";
import { JwModal } from "../dialog";

import styles from "./style.module.css";
import { ReactComponent as Logo } from "./../../static/icons/logo.svg";
export class Header extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      categories: [],
      cartProducts: [],
      currencies: [],
    };
  }

  fetchCategories = () => {
    CategoriesService.fetchCategories().then((result) => {
      CategoriesStore.setCategoriesData(result.data.categories);
      this.setState({
        categories: result.data.categories,
        currencies: result.data.currencies,
      });
    });
  };

  componentDidMount() {
    this.fetchCategories();
    this.categoryChange();
    CartStore.addChangeListener(this._onChange);
    CategoriesStore.addChangeListener(this.categoryChange);
  }

  componentWillUnmount() {
    CartStore.removeChangeListener(this._onChange);
    CategoriesStore.removeChangeListener(this.categoryChange);
  }

  _onChange = () => {
    const cartProducts = CartStore.getCart();
    this.setState({ cartProducts });
  };

  categoryChange = () => {
    const selectedCategory = CategoriesStore.getCategory();
    this.setState({ selectedCategory });
  };

  selectCategory(category) {
    CategoriesStore.selectCategory(category);
    CategoriesStore.emitChange();
  }

  setCurrency(event) {
    CurrencyStore.setCurrency(event.target.selectedOptions[0].value);
  }

  render() {
    return (
      <header>
        <div className={styles.header_wrapper}>
          <nav>
            <ul className={styles.header_list}>
              {this.state.categories.map((category) => (
                <li
                  key={category.name}
                  className={styles.header_item}
                  onClick={this.selectCategory.bind(this, category.name)}
                >
                  <NavLink
                    to={`/${category.name}`}
                    className={
                      styles.header_link +
                      " " +
                      (this.state.selectedCategory === category.name
                        ? styles.header_link_active
                        : "")
                    }
                  >
                    {category.name}
                  </NavLink>
                </li>
              ))}
            </ul>
          </nav>
          <div className={styles.header_logo}>
            <NavLink to="/">
              <Logo />
            </NavLink>
          </div>
          <div className={styles.header_information}>
            <SelectCurrency currencies={this.state.currencies} />
            <JwModal cartProducts={this.state.cartProducts} />
          </div>
        </div>
      </header>
    );
  }
}
